import Vue from 'vue'
import VueRouter from 'vue-router'
import Top from '../views/Top.vue'
import Introduction from '../views/Introduction.vue'
import Staff from '../views/Staff.vue'
import Comment from '../views/Comment.vue'
import Movie from '../views/Movie.vue'
import Theater from '../views/Theater.vue'
import News from '../views/News.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'top',
    component: Top
  },
  {
    path: '/introduction',
    name: 'introduction',
    component: Introduction
  },
  {
    path: '/staff',
    name: 'staff',
    component: Staff
  },
  {
    path: '/comment',
    name: 'Comment',
    component: Comment
  },
  {
    path: '/movie',
    name: 'movie',
    component: Movie
  },
  {
    path: '/theater',
    name: 'theater',
    component: Theater
  },
  {
    path: '/news',
    name: 'news',
    component: News
  },
  { // 存在しないURLの場合、HomeViewへリダイレクト
    path:'*',
    redirect:'/'
  }
]

const router = new VueRouter({
  mode: 'history',
  base: '/shirakawa.github.io/03/',
  routes
})

// 画面幅が600px以下であった場合、ルート遷移時に
// ハンバーガーアイコンをクリックしメニューを閉じる。
router.afterEach(() => {
  console.log('幅600px以下で初回読み込み時エラーが発生しますが仕様上問題はありません。\nデータを扱うならどんだけ小規模な見込みでもとりあえずVuexは入れておいたほうがよさそう。どうせ増えるので(遺言)');
  const closeMenuCheck = window.matchMedia('(max-width: 600px)');
  const closeTarget = document.getElementById('hamburger');
  if(closeMenuCheck.matches){
    closeTarget.click();
  }
})


export default router
