<template>
  <!-- 動画背景コンポーネント -->
  
  <div class="bg-wrap">
    <!-- トップページ以外では背景動画にフィルターを被せる -->
    <transition name="fade" mode="out-in" appear>
      <div class="bg-filter" v-if="!this.$route.path.match(/\/$/)"></div>
    </transition>
    <video class="bg-video" src="@/assets/background.mp4" muted autoplay loop preload="none"></video>
  </div>
</template>

<script>
export default {
  name: 'BackGroundVideo',
}
</script>

<style scoped>
.bg-wrap{
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: -1000;
}

.bg-filter {
	content: '';
	display: block;
	position: fixed;
	top: 0;
	right: 0;
	width: 100%;
	height: 100vh;
	background-color: rgba(0,0,0,0.5);
	background-image: radial-gradient(#111 30%, transparent 31%), radial-gradient(#111 30%, transparent 31%);
	background-size: 4px 4px;
	background-position: 0 0, 2px 2px;
  z-index: -1001;
}

.bg-video{
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  transform: translateX(-50%) translateY(-50%);
  z-index: -1002;
}



</style>
