<template>
  <!-- ナビ関係を纏めたコンポーネント -->
  <div>
    <nav class="global-nav">
      <div class="scroll-button-area" v-show="checkWidth">
        <PageTopButton/>
      </div>
      <!-- ボタンクリックでshowMenuを切り替え -->
      <button id="hamburger" @click="showMenu = !showMenu">
        <HamburgerIcon/> 
      </button>
      <transition name="fade" mode="out-in">
        <!-- showMenuを参照して表示の切り替え -->
        <div class="wrap-router" v-if="showMenu">
          <router-link to="/top">Top</router-link>
          <router-link to="/introduction">Introduction</router-link>
          <router-link to="/staff">Staff</router-link>
          <router-link to="/comment">Comment</router-link>
          <router-link to="/movie">Movie</router-link>
          <router-link to="/theater">Theater</router-link>
          <router-link to="/news">News</router-link>
        </div>
      </transition>
    </nav>
  </div>
</template>

<script>
import HamburgerIcon from '../atoms/HamburgerIcon.vue'
import PageTopButton from '@/components/PageTopButton.vue'

export default {
  name: 'GlobalMenu',
  data(){
    return{
      showMenu: true,
      checkWidth: window.matchMedia('(max-width: 600px)')
    }
  },
  /////////////////////////////////////////
  //// ここからブレークポイント関係の処理 ////
  //// showMenuの制御の都合上ここに記述  ////
  /////////////////////////////////////////

  // mountedでmatchMediaを格納し、イベントリスナーに登録
  // nextTickで後述のfuncを実行
  mounted(){
    this.$nextTick(()=>{
      
      this.func(this.checkWidth);
      this.checkWidth.addEventListener("change", this.func);
    });
  },

  // matchMedia変更の度に走る処理
  methods: {
    func(checkWidth) {
      if (checkWidth.matches) {
      // ここから幅600px""""以下""""の場合
        // showMenuをfalseに設定
        this.showMenu = false;
        // ハンバーガーメニュを表示
        document.getElementById('hamburger')
          .style.visibility = 'visible';
        // global-navの背景を透明にする
        document.getElementsByClassName('global-nav')[0]
          .style.backgroundColor = 'rgba(0,0,0,0)';
      } else {
      // ここから幅601px""""以上""""の場合
        // ハンバーガーメニュを非表示
        document.getElementById('hamburger')
          .style.visibility = 'hidden';
        // global-navの背景を黒にする
        document.getElementsByClassName('global-nav')[0]
          .style.backgroundColor = 'rgba(0,0,0,.8)';
        // showMenuをtrueに設定
        this.showMenu = true;
      }
    }
  },
  /////////////////////////////////////////
  //// ここまでブレークポイント関係の処理 ////
  /////////////////////////////////////////

  // wrap-router表示中にbodyにlockクラスを追加するwatch
  watch:{
    showMenu:{
      handler(){
        if(this.showMenu){
          document.body.className = 'lock';
        } else {
          document.body.className = '';
        }
      }
    }
  },
  components:{
    HamburgerIcon,
    PageTopButton
  }
}
</script>

<style scoped>
.global-nav {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 7vh;
  margin-bottom: 5vh;
  padding: 0 6vw;
  z-index: 5000;
  background-color: rgba(0,0,0,.8);
}

#hamburger{
  position: absolute;
  border: transparent;
  background: transparent;
  visibility: hidden;
  width: 35px;
  height: 35px;
  padding: 0;
  z-index: 5002;
}

.wrap-router{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  height: auto;
  margin-left: 29vw;
  z-index: 5001;
}


.wrap-router a {
  color: #dce3eb;
  text-decoration: none;
  font-size: 1.3rem;
}

.wrap-router a.router-link-exact-active {
  color: #16b8f3;
}

/* 画面幅1200pxの設定 */
@media screen and (max-width:1200px){
  .wrap-router{
    margin-left: 10vw;
  }
  .wrap-router a {
    font-size: 1rem;
  }
}

/* 画面幅600pxの設定 */
@media screen and (max-width:600px){
  .global-nav{
    padding: 0;
    position: relative;
  }

.scroll-button-area{
    position: fixed;
    top: 2.5rem;
    right: 1.5rem;
  }

  #hamburger{
    margin: 1.5rem 1.5rem 0 0;
    right: 0;
  }

  .wrap-router{
    position: fixed;
    width: 100vw;
    height: 60vh;
    margin: 0;
    padding-top: 6vh;
    top: 0;
    background-color: rgba(6,6,6,1);
    flex-flow: column;
    justify-content: space-around;
  }

  .wrap-router a{
    width: 100%;
    font-size: 6vw;
  }
}

</style>
