<template>
  <div>
    <!-- vue-js-modalの表示および設定 -->
    <modal name="first-modal"
    class="first-modal"
    :draggable="false"
    :resizable="false"
    :adaptive="true"
    :clickToClose="false"
    transition="modalTransition"
    overlayTransition="modalTransition"
    width="50%"
    height="50%"
    styles="
      background-color: rgba(0,0,0,0);
      border-radius: 0px;
      overflow: visible;
      box-shadow: 0 0px 0px 0 rgba(0,0,0,0);
      "
    >
    
      <!-- モーダルの内容物 -->
      <div class="modal-body">
        <!-- Loading.vueコンポーネントの読み込み -->
        <Loading v-if="isLoading"></Loading>
        <!-- Loading.vueの表示をいい感じにするための苦肉の策 -->
        <transition name="modalTransition">
          <div class="loading-bg" v-show="isLoading"></div>
        </transition>
        <!-- 動画部分 -->
        <video src="@/assets/Teaser.mp4"
        poster="@/assets/Teaser_thumb.png"
        controls
        preload="auto"></video>
        <!-- ウィンドウを閉じるボタン -->
        <button class="modal-close" @click="hide">
          <BatsuIcon/>
        </button>
      </div>  
    </modal>
  </div>
</template>

<script>
import Loading from '../atoms/Loading.vue'
import BatsuIcon from '../atoms/BatsuIcon.vue'

export default {
  // モジュール名をエクスポート
  name: 'FirstViewModal',
  data:()=>({
    isLoading: true,
  }),
  // 初回ロード時にモーダルを表示する
  mounted(){
    this.$modal.show('first-modal');
    // モーダル表示時にbodyにクラスを追加
    document.body.className = 'lock';
    // ロード完了時(今回は指定秒数経過)でLoading.vueを消す
    setTimeout(() => {
      this.isLoading = false
    }, 1000);
  },
  // モーダルを閉じるメソッド
  methods: {
    hide : function () {
      this.$modal.hide('first-modal');
      // モーダルを閉じた際にbodyのクラスを削除
      document.body.className = '';
    }
  },
  components:{
    Loading,
    BatsuIcon
  }
}
</script>

<style scoped>
/* モーダルでしか使わないトランジションなのでここに記述 */
.modalTransition-enter,
.modalTransition-leave-to{
  opacity: 0;
}

.modalTransition-leave-active{
  transition: opacity 1.5s;
}
/* ここまでモーダル専用トランジション */

.loading-bg{
  position: fixed;
  background:black;
  width: 100vw;
  height: 100vh;
  z-index: 5002;
}

.first-modal{
  background-color: rgba(0,0,0,.9);
  width: 100vw;
  height: 100vh;
  z-index: 8001;
  overscroll-behavior: contain;
}

.modal-body{
      z-index: 5000;
      display: flex;
      flex-flow: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: auto;
}

video{
  width: 100%;
  height: auto;
}

.modal-close{
  position: absolute;
  width: 2em;
  height: 3em;
  top: -10%;
  right: -45px;
  border: transparent;
  background: transparent;
  padding: 0;
}
</style>
