<template>
  <!-- サイトロゴだったりバナーだったりを表示する領域 -->
  <div class="campaign-area">

    <!-- バナーはv-forで回す -->
    <ul>
      <li v-for="banner in banners" :key="banner">
        <img :src="banner" alt="banner">
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'CampaignArea',
  // バナーのURLを格納
  data:()=>({
    banners: [
      require('@/assets/kari-banner1.svg'),
      require('@/assets/kari-banner2.svg'),
      require('@/assets/kari-banner3.svg')
    ]
  })
}
</script>

<style scoped>
  .campaign-area{
    position: fixed;
    width: auto;
    max-width: 25vw;
    padding: 10vh 0 0 0;
    height: auto;
  }

  ul{
    height: auto;
    list-style: none;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }

  li{
    height: auto;
    padding: 3vh 0;
  }


  img{
    width: 100%;
    height: auto;
  }

  @media screen and (max-width:600px){
  .campaign-area{
    width: 100vw;
    max-width: 100%;
    margin-right: 0;
    position: relative;
  }
}
</style>
