<template>
  <!-- Font Awesomeで使える☓アイコンがイマイチだったので自作したコンポーネント -->
  <div>
    <span class="batsu-icon"></span>
  </div>
</template>

<script>
export default {
  name: 'BatsuIcon'
}
</script>

<style scoped>
.batsu-icon{
  display: block;
  width: 100%;
  height: 100%;
}

.batsu-icon::before,
.batsu-icon::after{
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 1px;
  background: white;
}

.batsu-icon::before{
  transform: translate(-50%,-50%) rotate(45deg);
}

.batsu-icon::after{
  transform: translate(-50%,-50%) rotate(-45deg);
}

</style>
